import React from "react"
import {Link} from 'gatsby'
import { GatsbyImage,getImage } from "gatsby-plugin-image"
import { articleEntryCard,articleEntryLink } from './articleEntry.module.scss' 

const ArticleEntry = ({mdxNode}) =>{ 
    let {id,slug} = mdxNode
    let {title, shortDesc, date, category, tags, heroImg, heroImgAlt } = mdxNode.frontmatter

    return ( 
        <div className={`card border border-secondary mb-5 ${articleEntryCard}`}>
            <Link to={`/article/${slug}`} className={`${articleEntryLink}`}>
                <GatsbyImage className="card-img" 
                    image={getImage(heroImg)} alt={heroImgAlt} 
                    style={{maxHeight:'200px'}}
                    imgStyle={{width:'auto', margin: 'auto'}}/>

                <div className="card-body">
                    <span style={{color:'gray',fontSize:'smaller'}}>[ {category} ]</span> 
                    <div className="card-title h4">{title}</div>
                    <div className="card-text">{shortDesc}</div>
                </div>
            </Link>

            <div className="card-footer">
                <div className="row" style={{fontSize:'small'}}>
                    <div className="col">
                        <div style={{fontSize:'small'}}>
                            標籤: 
                            { tags?.map(tag=>{
                                return (
                                    <Link to={`/tag/${tag}`}  className="px-1" key={tag}>
                                        {`#${tag}`}
                                    </Link>
                                )
                            })}
                        </div>
                    </div>

                    <div className="col d-flex align-items-center justify-content-end">
                        <span className="small text-secondary">貼於 {date}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ArticleEntry