import * as React from 'react'
import {graphql,Link, useStaticQuery} from 'gatsby'
import Layout from '../../components/layout'
import ArticleList from '../../components/articleList'

const AllArticleListPage = ()=>{    
    const data = useStaticQuery(query)

    return (
        <Layout pageTitle="所有貼文">
            <ArticleList mdxNodes={data.allMdx.nodes} />
        </Layout>
    )
}

const query = graphql`
    query {
        allMdx(sort: {fields: [frontmatter___date,slug] , order: [DESC,DESC] }) {
            nodes {
                id, slug, 
                frontmatter {
                    date(formatString: "YYYY/MM/DD")
                    title
                    shortDesc
                    category
                    tags
                    heroImg{
                        childImageSharp {
                          gatsbyImageData
                        }
                    }
                    heroImgAlt
                }
            }
        }
    }
`


export default AllArticleListPage
